const Home = () => {
  return (
    <section className="home" id="home">
      <div className="content">
        <span data-aos="fade-up" data-aos-delay="100">
          Paris
        </span>
        <h3 data-aos="fade-up" data-aos-delay="200">
          Kinh  thi trang
        </h3>
        <p data-aos="fade-up" data-aos-delay="300">
          Paris l mt vng t ca ngh thut v ngh s. Thnh ph y nhng
          di tch c tm quan trng trong lch s ca thnh ph v th gii.
          Lch s phong ph ca thnh ph ny thu ht rt nhiu nhng ngi am
          m kho c v nhng ngi yu thch kin trc. Mt s di tch kin
          trc ni ting l Boulevard Montmartre c xy dng vo nm 1897,
          Basilique du Sacr-Cur  Montmartre, bo tng Louvre ni
          ting,Panthon
        </p>
        <a
          data-aos="fade-up"
          data-aos-delay="450"
          href="#destination"
          className="btn"
        >
          Tm hiu thm
        </a>
      </div>
    </section>
  );
};

const Bookform = () => {
  return (
    <section className="book-form" id="book-form">
      <form action="">
        <div data-aos="zoom-in" data-aos-delay="50" className="inputBox">
          <span style={{ fontSize: 2.5 + "em" }}>
            <h1>Chuyn i c t nhiu nht</h1>
          </span>
        </div>

        <div data-aos="zoom-in" data-aos-delay="100" className="inputBox">
          <span>
            <Link className="btn btn-book" to="/paris/book">
              t ngay
            </Link>
          </span>
        </div>
      </form>
    </section>
  );
};
const About = () => {
  const handleVidbtn = (e) => {
    document
      .querySelectorAll(".about .video-container .controls .control-btn")
      .forEach((btn) => {
        let src = btn.getAttribute("data-src");
        document.querySelector(".about .video-container .video").src = src;
      });
  };
  return (
    <section className="about" id="about">
      <div
        className="video-container"
        data-aos="fade-right"
        data-aos-delay="150"
      >
        <video
          src="/videos/vid-1.mp4"
          muted
          autoPlay
          loop
          className="video"
        ></video>
        <div className="controls">
          <span
            className="control-btn"
            data-src="/videos/vid-1.mp4"
            onClick={handleVidbtn}
          ></span>
          <span
            className="control-btn"
            data-src="/videos/vid-2.mp4"
            onClick={handleVidbtn}
          ></span>
          <span
            className="control-btn"
            data-src="/videos/vid-3.mp4"
            onClick={handleVidbtn}
          ></span>
        </div>
      </div>
      <div className="content" data-aos="fade-left" data-aos-delay="200">
        <span>Ti sao nn chn Stravel</span>
        <h3>Tri nghim bc nht</h3>
        <p>
          chng ti hp tc vi nhng a im ngh dng c tri nghim bc
          nht  em li mt k ngh tuyt vi cho bn cng ngi thn
        </p>
        <Link to="/aboutus" className="btn">
          Tm hiu thm v Stravel
        </Link>
      </div>
    </section>
  );
};
