import Header from "./Header2";
import Footer from "./Footer2";
import { Link } from "react-router-dom";
import { useEffect } from "react";

import "../../../Style/pages/Paris/night.css";
import "../../../Style/pages/Paris/style.css";

const Home = () => {
    useEffect(() => {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    }, []);
  return (
    <section className="home" id="home">
      <div className="content">
        <span data-aos="fade-up" data-aos-delay="100">
          Paris
        </span>
        <h3 data-aos="fade-up" data-aos-delay="200">
          vng t ca Pharaohs
        </h3>
        <p data-aos="fade-up" data-aos-delay="400">
          Paris l mt trong nhng quc gia lu i nht trn th gii. y la
          ni ngi Ai Cp c i  xy dng mt nn vn minh v i trong
          thung lng sng Nile, vi rt nhiu n th, c vt v nhng tn tch
          ngon mc ang  ban khm ph
        </p>
        <Link
          data-aos="fade-up"
          data-aos-delay="500"
          to="/Paris"
          className="btn"
        >
          Tm hiu thm
        </Link>
      </div>
    </section>
  );
};

const Book = () => {
  return (
    <section className="book-form" id="book-form">
      <form action="">
        <div data-aos="zoom-in" data-aos-delay="50" className="inputBox">
          <span>
            <h1>chuyn i c t nhiu nht</h1>
          </span>
        </div>
        <div data-aos="zoom-in" data-aos-delay="100" className="inputBox">
          <span>S in thoi</span>
          <input type="text" placeholder="0912345678" maxLength={11} />
        </div>
        <div data-aos="zoom-in" data-aos-delay="150" className="inputBox">
          <span>Email</span>
          <input type="email" placeholder="email@gmail.com" />
        </div>
        <div data-aos="zoom-in" data-aos-delay="200" className="inputBox">
          <span>Thi gian i</span>
          <input type="datetime-local" />
        </div>
        <div data-aos="zoom-in" data-aos-delay="250" className="inputBox">
          <span>Ti c</span>
          <input type="number"  min={2} max={8} placeholder="T 2-8 ngi" />
        </div>
        <div data-aos="zoom-in" data-aos-delay="300" className="inputBox">
          <span>Ti thanh ton bng</span>
          <select>
            <option value="momo">Momo</option>
            <option value="bank">Chuyn khon</option>
            <option value="card">Th VISA</option>
            <option value="none">Thanh ton khi n ni</option>
          </select>
        </div>
        <Link
          data-aos="zoom-in"
          data-aos-delay="350"
          className="btn"
          to="/success"
        >
          t Ngay
        </Link>
      </form>
    </section>
  );
};

export default function ParisBook() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div className="Pages Paris">
      <Header />
      <div className="container">
        <Home />
        <Book />
      </div>
      <Footer />
    </div>
  );
}
